/**
 * @fileOverview random layout
 * @author shiwu.wyy@antfin.com
 */

import { PointTuple, OutNode, Edge } from './types'
import { Base } from './base'

/**
 * 
 */
export class RandomLayout extends Base {
  /**  */
  public center: PointTuple = [0, 0]

  /**  */
  public width: number = 300

  /**  */
  public height: number = 300

  public nodes: OutNode[] = []

  public edges: Edge[] = []
  
  /**  */
  public onLayoutEnd: () => void = () => { }

  constructor(options?: RandomLayout.RandomLayoutOptions) {
    super()
    this.updateCfg(options)
  }

  public getDefaultCfg() {
    return {
      center: [0, 0],
      width: 300,
      height: 300,
    }
  }

  /**
   * 
   */
  public execute() {
    const self = this
    const nodes = self.nodes
    const layoutScale = 0.9
    const center = self.center
    if (!self.width && typeof window !== 'undefined') {
      self.width = window.innerWidth
    }
    if (!self.height && typeof window !== 'undefined') {
      self.height = window.innerHeight
    }

    if (nodes) {
      nodes.forEach((node) => {
        node.x = (Math.random() - 0.5) * layoutScale * self.width + center[0]
        node.y = (Math.random() - 0.5) * layoutScale * self.height + center[1]
      })
    }
    
    if (self.onLayoutEnd) self.onLayoutEnd()

    return {
      nodes,
      edges: this.edges,
    }
  }

  public getType() {
    return 'random'
  }
}

export namespace RandomLayout {
  export interface RandomLayoutOptions {
    type: 'random'
    center?: PointTuple
    width?: number
    height?: number
    workerEnabled?: boolean
    onLayoutEnd?: () => void
  }
}
